.home-page {
    height: 100%;
    position: relative;
    z-index: 1;
}

header {
    position: relative;
    z-index: 5;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 4rem;
}

.header-container {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}

.header-container .app-title {
    letter-spacing: 4px;
    font-size: 1.2rem;
}

.cart-icon {
    position: relative;
}

.cart-icon-pair {
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

@media (max-width: 775px) {
    header {
        padding: 0 2rem;
    }
}
